.methodFocus {
  width: 100%;
  display: grid;
  grid-template-rows: repeat(2, 56px);
  grid-template-columns: 55px auto;
  grid-column-gap: 14px;
  margin: 0 0 84px 0;
}

.methodFocus__number {
  grid-row: 1/3;
  margin: 0;
  color: #ECECEC;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: "Proxima Nova";
  font-size: 100px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.methodFocus__title {
  margin: 8px 0 0 0;
  align-self: center;
  color: #0077FF;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: "Proxima Nova";
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.methodFocus__subtitle {
  margin: 0;
  color: var(--Text-titleText, #454545);
  font-variant-numeric: lining-nums proportional-nums;
  font-family: "Proxima Nova";
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

@media screen and (max-width: 568px) {
  .methodFocus {
    margin: 10px 0 116px 0;
  }
}/*# sourceMappingURL=MethodFocus.css.map */