.nameContainer {
    max-height: 88px;
    min-height: 74px;
    width: 212px;
    background-color: #F3F3F3;
    padding: 16px;
    box-sizing: border-box;
    position: absolute;
    bottom: 76px;
    color: #171A28;
    z-index: 2;
}

.nameContainerTitle {
    margin: 0 0 4px 0;
    font-family: Proxima Nova;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.nameContainerSubtitle {
    margin: 0;
    font-family: Proxima Nova;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

@media screen and (max-width: 984px) {
    .nameContainer {
        left: 0;
        margin: 0 auto;
    }
}

@media screen and (max-width: 568px) {
    .nameContainer {
        width: 107px;
    }
}