.statElement {
  display: flex;
  flex-direction: column;
  width: 256px;
  padding: 45px 15px 22px 15px;
  box-sizing: border-box;
  align-items: center;
  border-radius: 25px;
  background: rgba(255, 255, 255, 0.6);
  box-shadow: 0px 0px 23px 0px rgba(0, 0, 0, 0.35);
}

.statElement__title {
  margin: 0 0 10px 0;
  color: var(--Header, #07F);
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: Proxima Nova;
  font-size: 70px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  word-spacing: -8px;
}

.statElement__subtitle {
  margin: 0;
  color: var(--Header, #07F);
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: Proxima Nova;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.statElement__span {
  color: var(--Header, #07F);
  font-size: 43px;
  margin: 0;
}/*# sourceMappingURL=StatElement.css.map */