.feedbackFormContainer {
  padding: 5px;
  margin: 28px 0 0 0;
  border-radius: 4px;
  border: 1px solid #F3F3F3;
  background: #FFF;
  box-shadow: 0px 45px 25px -20px rgba(174, 197, 191, 0.22);
  z-index: 1;
}

.feedbackForm {
  height: 100%;
  border: 1px dashed #D2D1D1;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 24px;
  padding: 12px 8px 10px 8px;
}

.feedbackForm__field {
  width: 100%;
  padding: 0;
  margin: 0;
}

.feedbackFormInput {
  width: 100%;
  height: 48px;
  padding: 11px 5px 9px 21px;
  border: none;
  border-radius: 5px;
  box-sizing: border-box;
  background: #F9F9F9;
}

.feedbackFormInput_message {
  height: 158px;
}

.feedbackFormBtnContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 120px;
}

.feedbackFormSubmitBtn {
  display: flex;
  width: 100%;
  height: 48px;
  padding: 22px 28px 20px 28px;
  margin: 0 0 15px 0;
  justify-content: center;
  align-items: center;
  grid-row: 2/3;
  grid-column: 1/2;
  border: none;
  border-radius: 4px;
  background: #3390EC;
  box-shadow: 0px 20px 24px -10px rgba(0, 119, 255, 0.3);
  transition: 0.25s;
  cursor: pointer;
  color: #FFF;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: "Proxima Nova";
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  font-variant: small-caps;
}
.feedbackFormSubmitBtn:hover {
  background: var(--Buttons, #539de8);
  box-shadow: 0px 24.8px 29.76px -12.4px rgba(51, 144, 236, 0.35);
}
.feedbackFormSubmitBtn:active {
  transform: translateX(1px) translateY(1px);
}

.feedbackFormLegalText {
  grid-column: 1/2;
  grid-row: 2/3;
  align-self: flex-end;
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: "Proxima Nova";
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  font-variant: all-small-caps;
}

.feedbackFormLegalText__link {
  color: #0077FF;
}

@media screen and (max-width: 984px) {
  .feedbackFormContainer {
    max-width: 660px;
    height: 100%;
    margin: 0;
  }
  .feedbackForm {
    display: flex;
    flex-direction: column;
  }
  .feedbackFormTitle {
    margin: 26px 17px;
  }
  .feedbackFormInputContainer {
    margin: 0 17px;
    gap: 20px;
  }
  .feedbackFormInput {
    width: 100%;
  }
  .adFormBtnContainer {
    margin-top: 20px;
    padding: 0 0 20px 0;
  }
  .feedbackFormLegalText {
    align-self: center;
  }
}
@media screen and (max-width: 568px) {
  .feedbackFormContainer {
    max-width: 345px;
  }
  .feedbackFormBtnContainer {
    align-self: center;
    max-width: 296px;
  }
}/*# sourceMappingURL=FeedbackForm.css.map */